.getMovies {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: black;
  box-sizing: border-box;

  button {
    position: fixed;
    left: 30px;
    top: 30px;
    height: 50px;
    width: 100px;
    background-color: tomato;
    color: white;
    border-radius: 10px;
    font-size: 20px;
    font-weight: 800;
    &:hover {
      background-color: green;
    }
  }
  div {
    display: flex;
    justify-content: center;
    background-color: azure;

    width: 700px;
    border-radius: 50px;

    img {
      width: 300px;
      height: 500px;
    }

    div {
      flex-direction: column;
      padding: 0px;
      margin-left: 5px;

      p {
        font-size: 20px;
      }
    }
  }
}
